          color: #f3f6fb;
        }
      }

      border-bottom: 1px solid rgba(226, 232, 245, 0.1);
    }

    .media-contact-name span:first-child {
      color: #fff !important;
    }

    .media.selected {
      background-color: rgba(243, 246, 251, 0.1);
